var pintuan_home_id = 0;
var countDown = 2;
var app = getApp();
var shareData = [];
export default {
	data() {
		return {
			cdnUrl: app.globalData.cdnUrl,
			table_number: 1,
			alertShow: 2, // 1显示 2隐藏
			options: '',
			hbDatas: '',
			recordList: [],
			recordRand: [],
			pages: 0,
			morenavatar: '/static/redbag/avatar.png'
		}
	},
	onLoad(options) {
		console.log(options);
		var that = this;
		that.options = options;
		that.showHb();
		that.getHBRecord();
	},

	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		this.countDown = 1;
		// this.getPintuan();
	},
	onHide() {
		this.countDown = 1;
	},
	onUnload() {
		this.countDown = 1;
	},
	onShow() {
		var that = this;
		this.countDown = 2;
		if (this.countDown == 1) {
			that.showHb();
		}

	},
	onShareAppMessage() {
		var that = this;
		var title = that.shareData.share_title;
		var path = "/pages/index/index?tid=" + that.shareData.id +
			"&jump=pages/jointeam/jointeam_redbag&jumpData1=action&jumpData2=other&jumpData3=pintuan_home_id&jumpData4=" + that
			.hbDatas.row.pintuan_home_id + "&jumpData5=hid&jumpData6=" + that.options.hid;
		var imageUrl = that.shareData.share_img;
		console.log(path);
		return {
			title: title,
			path: path,
			imageUrl: imageUrl,
		}
	},
	methods: {
		tixian() {
			var that = this;
			if (parseInt(that.hbDatas.row.totalMoney) >= 200) {
				// 可以提现
				this.$http.post("hbTixian", {
					hid: that.options.hid
				}, {
					'showErrorMsg': false
				}).then((res) => {
					if (res.code == 1) {
						uni.showModal({
							title: "温馨提示",
							showCancel: false,
							content: "提现成功",
							success(res) {
								uni.switchTab({
									url: "/pages/index/index",
								})
							}
						})
					} else {
						uni.showModal({
							title: "温馨提示",
							showCancel: false,
							content: res.msg,
							success(res) {}
						})
					}
				}).catch(err => {
					console.log('info,错误请求返回=', err)
				})
			} else {
				// 弹出窗口
				that.alertShow = 1;
			}
		},
		getHBRecord() {
			var that = this;
			this.$http.post("getHBRecord", {
				hid: that.options.hid,
				pages: that.pages
			}).then((res) => {
				that.recordList = that.recordList.concat(res.data.recordList);
				that.recordRand = that.recordRand.concat(res.data.recordRand);
				that.pages += 1;
			})
		},
		showHb() {
			var that = this;
			this.$http.post("showHB", {
				hid: that.options.hid
			}).then((res) => {
				that.hbDatas = res.data;
				that.shareData = res.data.share;
				setTimeout(function() {
					that.setCountDown();
				}, 500)
			})
		},
		alertstatus() {
			var that = this;
			if (that.alertShow == 1) {
				that.alertShow = 2;
			} else {
				that.alertShow = 1;
			}
		},
		changetable(e) {
			var that = this;
			that.table_number = e.currentTarget.dataset.index;
		},
		// 一个倒计时
		setCountDown() {
			var that = this;
			var time = 1000;
			var datas = this.hbDatas.row;
			if (datas.times <= 0) {
				datas.times = 0;
			}
			datas.times = datas.times * 1000;
			let formatTime = this.getFormat(datas.times);
			datas.times -= time;
			datas.h = `${formatTime.hh}`;
			datas.m = `${formatTime.mm}`;
			datas.s = `${formatTime.ss}`;
			datas.times = datas.times / 1000;
			that.hbDatas.row = datas;
			if (that.countDown == 2 && datas.status == 1) {
				setTimeout(this.setCountDown, time);
			}

		},

	}
}
